body {
  color: #111;
  background-color: #fafafa;
  font-family: Montserrat, sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.main {
  justify-items: center;
  display: flex;
}

.container {
  width: 1136px;
  height: 600px;
  padding: 0 32px;
}

.section {
  max-width: 1200px;
  padding-bottom: 128px;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

:root {
  --orange: #fd9222;
  --black: #111;
  --dark-purple: #1e1823;
  --white-gray: #fafafa;
  --white: #fff;
}

.header-content, .header-nav {
  display: flex;
}

a {
  color: var(--black);
  text-decoration: none;
}

.header-link:hover, .header-link:focus {
  color: var(--orange);
}

.header-icon {
  stroke: var(--black);
  fill: #0000;
}

.header-icon:hover, .header-icon:focus {
  stroke: var(--orange);
  cursor: pointer;
}

@media screen and (min-width: 375px) and (max-width: 767px) {
  .header-container {
    width: 375px;
  }

  .header-content {
    justify-content: space-between;
    padding: 25px 20px 19px;
  }

  .header-logo {
    letter-spacing: -.42px;
    align-items: center;
    gap: 4px;
    font-family: Montserrat;
    font-size: 14px;
    font-style: italic;
    font-weight: 700;
    line-height: 18px;
    display: flex;
  }

  .header-nav {
    display: none;
  }

  .header-icon {
    width: 21px;
    height: 21px;
  }

  .logo-icon {
    stroke: none;
  }

  .header-menu {
    stroke: var(--black);
    width: 28px;
    height: 28px;
    cursor: pointer;
    background-color: #0000;
    border: none;
    align-items: center;
    display: flex;
  }

  .header-menu-button {
    width: 28px;
    height: 28px;
    padding: 0;
  }

  .mobile-menu-container {
    width: 220px;
    background-color: var(--dark-purple);
    display: none;
    position: relative;
    overflow: hidden;
    transform: translateX(70%);
  }

  .mobile-menu-close {
    cursor: pointer;
    width: 28px;
    height: 28px;
    background-color: #0000;
    border: 0;
    margin-top: 20px;
    margin-left: 172px;
    padding: 0;
  }

  .mobile-menu-close-icon {
    width: 28px;
    height: 28px;
    stroke: var(--white);
  }

  .mobile-menu-div {
    width: 100%;
    flex-direction: column;
    display: flex;
  }

  .mobile-menu-nav {
    flex-direction: column;
    justify-content: center;
    margin-top: 88px;
    display: flex;
  }

  .mobile-menu-nav-links {
    width: auto;
    flex-direction: column;
    align-items: flex-start;
    gap: 24px;
    padding-left: 50px;
    display: flex;
  }

  .mobile-menu-nav-link {
    color: var(--white);
    letter-spacing: -.28px;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
  }

  .mobile-menu-nav-link:hover, .mobile-menu-nav-link:focus {
    color: var(--orange);
  }

  .mobile-menu-nav-icons {
    gap: 18px;
    margin-top: 450px;
    padding-bottom: 20px;
    padding-left: 20px;
    display: flex;
  }

  .mobile-menu-icon {
    stroke: var(--white);
    fill: #0000;
  }

  .mobile-menu-icon--instagram, .mobile-menu-icon--twitter {
    width: 22px;
    height: 22px;
  }

  .mobile-menu-icon:hover, .mobile-menu-icon:focus {
    stroke: var(--orange);
  }
}

